// src/pages/api/products/index.js
import dbConnect from "lib/dbConnect";
import CatalogService from "src/services/catalogService.js"; // adjust path if your project resolves differently

export default async function handler(req, res) {
  await dbConnect();

  if (req.method === "GET") {
    try {
      const { categoryId, limit = 20, page = 1, q = "", inStockOnly, storefront } = req.query;
      const filter = {};

      // If caller passed categoryId, compute descendants if your Category model supports path
      if (categoryId) {
        // keep behaviour simple here; if you have getDescendantCategoryIds helper, call it.
        filter.categories = { $in: [categoryId] };
      }
      if (storefront) filter.storefronts = storefront;
      if (inStockOnly === "true") filter["sizes.stock"] = { $gt: 0 };
      if (q) filter.$text = { $search: q };

      const skip = (Number(page) - 1) * Number(limit);
      const products = await CatalogService.getProducts(filter, { lean: true, limit: Number(limit), skip, sort: { createdAt: -1 } });
      const total = await (import("models/Product.js").then(m => m.default.countDocuments(filter)));
      return res.status(200).json({ items: products, total: await total });
    } catch (err) {
      console.error("GET /api/products error:", err && (err.stack || err));
      return res.status(500).json({ message: "Failed to fetch products" });
    }
  }

  res.status(405).json({ message: "Method not allowed" });
}
